:root {
  --primary: #0076ff;
  --accent: #5c5cff;
  --background: #e6f4ff;
  --text: #002a50;
  --neutral: #f5f5f7;
  --shadow: #0000001a;
  --shadow-hover: #0003;
}

.chat-container {
  background-color: var(--background);
  width: 100%;
  max-width: 500px;
  height: 85vh;
  max-height: 700px;
  box-shadow: 0 4px 16px var(--shadow);
  border-radius: 16px;
  flex-direction: column;
  font-family: Inter, -apple-system, BlinkMacSystemFont, sans-serif;
  display: flex;
  overflow: hidden;
}

.chat-header {
  text-align: center;
  color: var(--text);
  background-color: #fff;
  border-bottom: 1px solid #ddd;
  margin: 0;
  padding: 1.25rem;
  font-size: 1.35rem;
  font-weight: 700;
}

.chat-box {
  background-color: var(--neutral);
  border-bottom: 1px solid #ddd;
  flex: auto;
  padding: 2rem;
  overflow-y: auto;
}

.message-pair {
  margin-bottom: 1.5rem;
}

.user-msg {
  justify-content: flex-end;
  margin: .75rem 0;
  display: flex;
}

.user-msg .message-label {
  display: none;
}

.user-msg:before {
  content: "You:";
  color: var(--text);
  align-self: center;
  margin-right: .75rem;
  font-weight: 600;
}

.bot-msg {
  justify-content: flex-start;
  margin: .75rem 0;
  display: flex;
}

.bot-msg .message-label {
  display: none;
}

.bot-msg:before {
  content: "Clyde:";
  color: var(--text);
  align-self: center;
  margin-right: .75rem;
  font-weight: 600;
}

.user-msg, .bot-msg {
  color: var(--text);
  border-radius: 12px;
  padding: 1rem 1.25rem;
  font-size: 1rem;
  line-height: 1.5;
}

.user-msg {
  background: linear-gradient(90deg, var(--primary), var(--accent));
  color: #fff;
}

.bot-msg {
  box-shadow: 0 2px 4px var(--shadow);
  background-color: #fff;
  border: 1px solid #ddd;
}

.loading {
  color: var(--text);
  opacity: .7;
  text-align: center;
  margin: 1rem 0;
  font-style: italic;
}

.loading-dots {
  text-align: left;
  width: 24px;
  display: inline-block;
}

.loading-dots:after {
  content: "...";
  width: 24px;
  animation: 1.5s infinite dots;
  display: inline-block;
}

@keyframes dots {
  0%, 20% {
    content: ".";
  }

  40% {
    content: "..";
  }

  60%, 100% {
    content: "...";
  }
}

.faq-divider {
  color: var(--text);
  opacity: .8;
  margin: 1rem 0 .5rem;
  font-size: .95rem;
  font-weight: 600;
}

.faq-buttons {
  flex-direction: column;
  gap: .75rem;
  margin: 1rem 0;
  display: flex;
}

.faq-button {
  border: 1px solid var(--primary);
  cursor: pointer;
  text-align: left;
  width: 100%;
  color: var(--text);
  white-space: normal;
  word-wrap: break-word;
  background-color: #fff;
  border-radius: 8px;
  align-items: center;
  height: auto;
  min-height: 48px;
  padding: .75rem 1rem;
  font-size: .95rem;
  transition: all .2s;
  display: flex;
}

.faq-button:hover:not(:disabled) {
  background-color: var(--primary);
  color: #fff;
  transform: scale(1.02);
}

.faq-button:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.chat-form {
  background: #fff;
  border-top: 1px solid #ddd;
  flex-direction: column;
  gap: .75rem;
  padding: 1.25rem;
  display: flex;
}

.chat-input {
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 100%;
  padding: 1rem;
  font-size: 1rem;
  transition: all .2s;
}

.chat-input:focus {
  border-color: var(--primary);
  outline: none;
  box-shadow: 0 0 0 3px #0076ff1a;
}

.chat-input:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.chat-form-buttons {
  justify-content: flex-end;
  gap: .75rem;
  display: flex;
}

.chat-form-buttons button {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  padding: .75rem 1.5rem;
  font-size: 1rem;
  font-weight: 600;
  transition: all .2s;
}

.chat-form-buttons button[type="submit"] {
  background: linear-gradient(90deg, var(--primary), var(--accent));
  color: #fff;
}

.chat-form-buttons button[type="submit"]:hover:not(:disabled) {
  box-shadow: 0 4px 12px var(--shadow-hover);
  transform: scale(1.05);
}

.chat-form-buttons button[type="submit"]:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.chat-form-buttons button[type="button"] {
  background-color: var(--neutral);
  color: var(--text);
  border: 1px solid #ddd;
}

.chat-form-buttons button[type="button"]:hover:not(:disabled) {
  background-color: #e0e0e7;
  transform: scale(1.05);
}

.branding {
  color: var(--text);
  text-align: left;
  margin-top: .5rem;
  font-size: .75rem;
}

.branding-link {
  color: var(--primary);
  text-decoration: none;
  transition: color .2s;
}

.branding-link:hover {
  color: var(--accent);
  text-decoration: underline;
}

@media (width <= 768px) {
  .chat-container {
    border-radius: 0;
    max-width: 100%;
    height: 90vh;
    max-height: none;
  }

  .chat-header {
    padding: 1rem;
    font-size: 1.2rem;
  }

  .chat-box {
    padding: 1.25rem;
  }

  .user-msg, .bot-msg {
    padding: .75rem 1rem;
    font-size: .9rem;
  }

  .faq-button {
    min-height: 44px;
    padding: .5rem .75rem;
    font-size: .9rem;
  }

  .chat-form {
    padding: 1rem;
  }

  .chat-input {
    padding: .75rem;
    font-size: .9rem;
  }

  .chat-form-buttons button {
    padding: .5rem 1rem;
    font-size: .9rem;
  }
}

.modal-overlay {
  z-index: 2147483647 !important;
  background: #000000b3 !important;
  justify-content: center !important;
  align-items: center !important;
  width: 100vw !important;
  height: 100vh !important;
  display: flex !important;
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
}

.modal-content {
  z-index: 2147483647 !important;
  background: #fff !important;
  border-radius: 8px !important;
  width: 90% !important;
  max-width: 1000px !important;
  max-height: 80vh !important;
  padding: 20px !important;
  position: relative !important;
  overflow-y: auto !important;
  box-shadow: 0 2px 10px #0000001a !important;
}

.modal-content .modal-close {
  cursor: pointer;
  text-shadow: 0 0 2px #000;
  background: none;
  border: none;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
  color: red !important;
}

.modal-error {
  color: red;
  margin: 10px 0;
}

.calendar-container {
  margin: 10px 0;
}

.calendar-nav {
  background: #f8f9fa;
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  display: flex;
}

.calendar-nav button {
  color: #fff;
  cursor: pointer;
  background: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
  font-size: 16px;
  transition: background .2s;
}

.calendar-nav button:hover:not(:disabled) {
  background: #0056b3;
}

.calendar-nav button:disabled {
  cursor: not-allowed;
  background: #ccc;
}

.calendar-nav span {
  color: #333;
  font-size: 16px;
  font-weight: 500;
}

.calendar-table-wrapper {
  overflow-x: auto;
}

.calendar-table {
  flex-direction: column;
  display: flex;
}

.day-headers {
  border-bottom: 2px solid #dee2e6;
  justify-content: space-between;
  margin-bottom: 10px;
  padding-bottom: 5px;
  display: flex;
}

.day-header-cell {
  text-align: center;
  color: #333;
  flex: 1;
  min-width: 120px;
  font-size: 16px;
  font-weight: 600;
}

.day-slots {
  flex-direction: column;
  display: flex;
}

.slot-grid {
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
  display: grid;
}

.slot-row, .view-more-row {
  display: contents;
}

.day-column {
  text-align: center;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  padding: 10px;
}

.no-slots {
  color: #666;
  font-style: italic;
}

.slot, .slot-selected, .view-more {
  cursor: pointer;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 5px 0;
  padding: 8px;
  font-size: 14px;
  transition: all .2s;
  display: block;
}

.slot:hover, .slot-selected:hover, .view-more:hover {
  background: #e9ecef;
}

.slot-selected {
  color: #fff;
  background: #007bff;
  border-color: #0056b3;
}

.view-more {
  color: #fff;
  background: #007bff;
  border-color: #0056b3;
  margin-top: 10px;
}

.form {
  flex-direction: column;
  gap: 10px;
  margin-top: 1em;
  display: flex;
}

input, textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 12px;
  font-size: 16px;
  transition: border-color .2s;
}

input:focus, textarea:focus {
  border-color: #007bff;
  outline: none;
}

button {
  color: #fff;
  cursor: pointer;
  background: #007bff;
  border: none;
  border-radius: 4px;
  padding: 12px;
  font-size: 16px;
  transition: background .2s;
}

button:hover:not(:disabled) {
  background: #0056b3;
}

button:disabled {
  cursor: not-allowed;
  background: #ccc;
}

@media (width <= 1000px) {
  .modal-content {
    max-width: 90% !important;
  }

  .day-headers, .day-slots {
    flex-direction: column;
  }

  .day-header-cell, .day-column {
    min-width: 100%;
    margin-bottom: 10px;
  }

  .slot-grid {
    grid-template-columns: 1fr;
  }
}

.chat-widget-wrapper {
  z-index: 1000;
  flex-direction: column;
  align-items: flex-end;
  gap: 12px;
  display: flex;
  position: fixed;
  bottom: 24px;
  right: 24px;
}

.chat-widget-popup {
  width: 380px;
  height: 600px;
  overflow: unset;
  background: #fff;
  border-radius: 16px;
  flex-direction: column;
  animation: .3s ease-out slideUp;
  display: flex;
  box-shadow: 0 12px 32px #0000001f;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.chat-box {
  flex: auto;
  padding: 8px;
  overflow-y: auto;
}

.chat-form {
  background: #fafafa;
  border-top: 1px solid #eee;
  flex: none;
  padding: 8px;
}

.chat-bubble {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #2d3b8f, #4e5dbb);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  font-size: 1.6rem;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 8px 24px #0003;
}

.chat-bubble:hover {
  transform: scale(1.1);
  box-shadow: 0 10px 32px #0000004d;
}
